<template>
	<aside class="menu-aside">
		<!--导航菜单-->
		<el-menu
			:default-active="$route.path"
			background-color="#545c64"
			text-color="#fff"
			active-text-color="#ffd04b"
			router
			unique-opened
			v-show="!collapsed"
			>
			<template v-for="(item, index) in sideMenu">
				<el-submenu :index="index + ''" :key="index" v-if="item.childrens">
					<template slot="title"><i class="el-icon-menu"></i>{{item.menuName}}</template>
					<template  v-for="(child, index) in item.childrens">
						<el-menu-item v-if="!child.childrens" :key="index" :index="child.frontUrl === '/' ? '' : child.frontUrl">
							{{child.url}}
						</el-menu-item>
						<el-submenu v-else :index="index.toString()" :key="index">
							<template slot="title">{{child.menuName}}</template>
							<el-menu-item :key="item.frontUrl" v-for="item in child.childrens" :index="item.frontUrl === '/' ? '' : item.frontUrl">
								{{item.url}}
							</el-menu-item>
						</el-submenu>
					</template>
				</el-submenu>
				<el-menu-item v-else :index="item.frontUrl === '/' ? '' : item.frontUrl" :key="item.frontUrl">
					<i class="el-icon-menu"></i>
					{{item.url}}
				</el-menu-item>
			</template>
		</el-menu>
		<!--导航菜单-折叠后-->
		<ul class="el-menu el-menu-vertical-demo el-menu--darkcollapsed custom-menu" v-show="collapsed" ref="menuCollapsed">
			<li v-for="(item, index) in sideMenu" class="el-submenu item" :key="index">
				<template>
					<div class="el-submenu__title"
						style="padding-left: 10px;height: 50px; line-height: 40px;"
						@mouseover="showMenu(index,true)"
						@mouseout="showMenu(index,false)"
					>
						<i class="el-icon-menu"></i>
					</div>
					<ul class="el-menu submenu" :class="'submenu-hook-'+index" @mouseover="showMenu(index,true)" @mouseout="showMenu(index,false)">
						<li v-for="(child, index) in item.childrens"
							:key="index"
							class="el-menu-item"
							style="padding-left: 40px;"
							:class="$route.path == child.frontUrl ? 'is-active':''"
							@click="$router.push(child.frontUrl)"
							>
								{{child.menuName}}
						</li>
					</ul>
				</template>
			</li>
		</ul>
	</aside>
</template>
<script>
	import { mapState } from 'vuex';
	export default {
		props: ["collapsed"],
		data() {
			return {
			}
		},
		methods: {
			showMenu(i, status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none';
			}
		},
		computed: {
			...mapState(['sideMenu'])
		}
	}
</script>
<style lang="scss">
	.custom-menu {
		&.el-menu {
			background-color: #2a4139
		}
		.el-submenu:hover .el-submenu__title {
			background-color: #223830;
			color: white;
		}
		.submenu {
			.el-menu-item {
				&:hover {
					background-color: #223830;
				}
				color: white;
			}
			display: none;
			position: absolute;
    		margin-top: -50px;
			left: 39px;
			height: auto!important;
			background-color: #2a4139;
		}
	}
</style>



