	<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				{{collapsed?'':sysName}}
			</el-col>
			<el-col :span="10">
				<div class="tools" @click.prevent="collapse">
					<i class="el-icon-menu"></i>
				</div>
			</el-col>
			<el-col :span="4" class="userinfo">
				<span class="el-dropdown-link userinfo-inner">欢迎：{{sysUserName}}</span>
				<a class="logout" href="javascript:;" @click="logout">退出登录</a>
			</el-col>
		</el-col>
		<el-col :span="24" class="main" :class="collapsed?'menu-collapsed':'menu-expanded'">
			<sidebar :collapsed="collapsed"> </sidebar>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<!-- 内容区块 -->
					<el-col :span="24" class="content-wrapper">
						<transition :name="transitionName">
							<router-view class="child-view"></router-view>
						</transition>
					</el-col>
				</div>
			</section>
		</el-col>
	</el-row>
</template>

<script>
import sidebar from "./slidebar.vue";
import { ajaxPostCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";
import { mapMutations } from "vuex";

let username = "",
  menus = [];
if (window.global) {
  var nowData = window.global;
  username = nowData.username;
  try {
    menus = [
      {
        text: "内容管理",
        children: [
          { text: "待审核资讯列表", modulepath: "/unCheckList" },
          {
            text: "栏目",
            modulepath: "",
            children: [
              { text: "保险攻略", modulepath: "/columnList?type=baoxian" },
              { text: "健康", modulepath: "/columnList?type=health" },
              { text: "好医生", modulepath: "/columnList?tyep=doctor" }
            ]
          },
          { text: "专题管理", modulepath: "/specialmange" },
          { text: "栏目管理", modulepath: "/columnmanage" }
        ]
      },
      {
        text: "标签",
        children: [
          { text: "标签管理", modulepath: "/tag" },
          { text: "标签分类管理", modulepath: "/cate" }
        ]
      },
      {
        text: "爬虫目标管理",
        modulepath: "/target"
      },
      {
        text: "推荐算法参数配置",
        modulepath: "/math"
      }
    ];
  } catch (e) {
    menus = [];
  }
}

export default {
  data() {
    return {
      sysName: "资讯后台",
      collapsed: false,
      sysUserName: username,
      menus: menus,
      transitionName: "slide-left",
      menuData: []
    };
  },
  methods: {
    ...mapMutations(["initSideMenu"]),
    //退出登录
    logout: function() {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        window.location.href = "/logout";
      });
    },
    //折叠导航栏
    collapse: function() {
      this.collapsed = !this.collapsed;
    }
  },
  created() {
    ajaxPostCommon(ajaxUrl.sideMenu).then(res => {
      this.initSideMenu(res.result.menuList);
      this.sysUserName = res.result.userName;
    });
  },
  components: {
    sidebar
  },
  watch: {
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      this.transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
    }
  }
};
</script>

<style lang="scss">
$color-primary: #282828;
$sildebarWidth: 170px;
aside {
  overflow-y: scroll;
}
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .header {
    height: 40px;
    line-height: 40px;
    background: $color-primary;
    color: #fff;
    .userinfo {
      text-align: right;
      padding-right: 35px;
      float: right;
      .userinfo-inner {
        padding-right: 10px;
        color: #fff;
        img {
          width: 30px;
          height: 30px;
          border-radius: 20px;
          margin: 5px 0px 10px 10px;
          float: right;
        }
      }
      .logout {
        color: #ffffff;
        border-left: 1px solid #cccccc;
        padding-left: 10px;
        text-decoration: none;
      }
    }
    .logo {
      height: 40px;
      font-size: 20px;
      padding-left: 20px;
      padding-right: 20px;
      border-color: rgba(238, 241, 146, 0.3);
      border-right-width: 1px;
      border-right-style: solid;
      img {
        width: 40px;
        float: left;
        margin: 10px 10px 10px 18px;
      }
      .txt {
        color: #fff;
      }
    }
    .logo-width {
			text-align: center;
      width: $sildebarWidth;
    }
    .logo-collapse-width {
      width: 40px;
    }
    .tools {
      padding: 0px 23px;
      width: 14px;
      height: 40px;
      line-height: 40px;
      cursor: pointer;
    }
  }
  .main {
    display: flex;
    position: absolute;
    top: 40px;
    bottom: 0px;
    overflow: hidden;
    aside {
      width: $sildebarWidth;
      height: 100%;
      .el-menu {
        width: auto !important;
        z-index: 100;
        height: 100%;
				border: 0;
      }
      .el-menu-item {
        min-width: $sildebarWidth;
      }
      .collapsed {
        width: 40px;
        .item {
          position: relative;
        }
        .submenu {
          position: absolute;
          top: 0px;
          left: 40px;
          z-index: 99999;
          height: auto;
          display: none;
        }
      }
    }
    &.menu-collapsed {
      .menu-aside {
        width: 40px;
      }
      .content-container {
        left: 40px;
      }
    }
    &.menu-expanded {
      .menu-aside {
        flex: 0 0 $sildebarWidth;
        width: $sildebarWidth;
      }
      .content-container {
        left: $sildebarWidth;
      }
    }
    .content-container {
      position: absolute;
      left: $sildebarWidth;
      right: 0;
      height: 100%;
      overflow-y: auto;
      .grid-content {
        height: 100%;
        display: flex;
        flex-direction: column;
      }
      .content-wrapper {
        height: 100%;
        background-color: #f4f4f4;
        box-sizing: border-box;
      }
    }
  }
}
</style>



